<template>
<div>
    <nav-bar :name="title"></nav-bar>
    <v-card flat class="text-center">
        <v-container class="mx-10 text-center">
        <h4> {{ by }} | Referral </h4>
        <v-row class="mb-n8 text-center" v-for="(item, id) in options" :key="id">
            <v-col cols="12">
            <h6 class="text-left"> {{ item.text }} </h6>
                <v-text-field
                outlined
                dense
                disabled
                :value="item.value"
                ></v-text-field>
            </v-col>
        </v-row>
        </v-container>
    </v-card>
</div>
</template>

<script>
  import ReferralService from '@/services/ReferralService';
  import Swal from 'sweetalert2';
  import NavBar from '@/components/NavBar'

export default {
    components: {
        NavBar
    },

    data: () => ({
    title: 'VIEW REFERRAL',
    by: '',
        
    options: [
      {
        text: "Joined on",
        value: ""
      },
      {
        text: "Unique id",
        value: ""
      },
      {
        text: "Name",
        value: ""
      },
      {
        text: "Destination email",
        value: ""
      },
      {
        text: "Phone number",
        value: ""
      },
    ]
    }),

    computed:{
        id_referral(){
            return this.$route.params.id
        }
    },

    methods: {
        refresh_get(){
            ReferralService
                .get(this.id_referral)
                .then(res => {
                    this.by = res.by;
                    this.options[0].value = res.registered;
                    this.options[1].value = res.unique_id;
                    this.options[2].value = res.name;
                    this.options[3].value = res.destination_email;
                    this.options[4].value = res.phone_number;
                })
                .catch(e => {
                let error = e.response.data;
                let msg = ''
                Object.entries(error).forEach(([ key, value ]) => {
                    msg+= key + ': ' + value + ' \n ';
                });
                    Swal.fire({
                    icon: 'error',
                    title: 'Error',
                    text: msg
                    });
                })
        },
    },

    mounted() {
        this.refresh_get();
    },
    
}
</script>