import http from '@/assets/http-commom.js'
import Swal from 'sweetalert2'

class ReferralService {
    list_admin(){
        return http
            .get('referral/all/')
            .then(res => {
                return res.data
            })
    }

    list_referrals_store(pk){
        return http
            .get('referral/'+pk+'/stores/')
            .then(res => {
                return res.data
            })
    }

    list_user_referrals(pk){
        return http
        .get('referral/'+pk+'/user_referrals/')
        .then(res => {
            return res.data
        })
    }

    add_referral(data){
        return http
            .post('referral/', {
                store_owner: data.store_owner,
                name: data.name,
                destination_email: data.destination_email,
                phone_number: data.phone_number,
            })
            .then(res => {
                return res.data
            })
    }

    list_owners(){
        return http
        .get('stores_owner/')
        .then(res => {
            return res.data
        })
    }

    delete_referrals(data){
        return http
        .post('referral/remove_referrals/', {
            id_referrals: data
        })
    }
    
    update(data, pk){
        return http
            .put('referral/'+pk+'/', {
                store_owner: data.store_owner,
                name: data.name,
                destination_email: data.destination_email,
                phone_number: data.phone_number,
            })
            .then(res => {
                return res.data
            })
    }

    get(pk){
        return http
        .get('referral/'+pk+'/')
        .then(res => {
            return res.data
        })
    }

}

export default new ReferralService();